<template>
  <h1>1111{{ content }}</h1>
</template>
<script>
import { defineComponent, ref } from "vue";

export default defineComponent({
  setup() {
    let content = ref("Hello World");
    return new Promise((resolve) => {
      setTimeout(() => {
        return resolve(content);
      }, 3000);
    });
  },
});
</script>
<style lang='less' scoped>
</style>